import AgencyCard from "./AgencyCard";
import React, { Component } from "react";
import IAgenciesPage from "../../../../estreeh-common-library/build/interfaces/IAgenciesPage";
import ScrollToTop from "../common/ScrollToTop";
import FilteredResultsAlert from "../common/FilteredResultsAlert";

class AgenciesPage extends Component<IAgenciesPage, any> {
  render() {
    let agencies;
    if (this.props.agencies !== undefined) {
    
        agencies = this.props.agencies.map((agency) => (
          <AgencyCard
            key={agency._id}
            agency={agency}
            showContractInfo={this.props.showContractInfo}
            showTabChips={this.props.showRentedInfo}
          />
        ));
      
    } else {
      agencies = <React.Fragment>No agencies</React.Fragment>;
    }

    // TODO: If selected by month, message / button for "With stayover? I want stayover?" which links to safety information
    // TODO: Search bar

    return (
      <React.Fragment>
        <ScrollToTop />
        <FilteredResultsAlert/>
        {agencies}
      </React.Fragment>
    );
  }
}

export default AgenciesPage;
