import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@material-ui/core";
import { navigate } from "@reach/router";
import axios from "axios";
import React, { ReactElement } from "react";
import { toast } from "react-toastify";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";

interface Props {
  open: boolean;
  setOpen: React.Dispatch<React.SetStateAction<boolean>>;
}

export default function LogoutDialog(props: Props): ReactElement {
  const setUser = useStoreActions((a) => a.user.setItem);

  const logoutConfirmed = () => {
    axios
      .get(process.env.REACT_APP_API_ROOT + "/auth/logout", {
        withCredentials: true,
      })
      .finally(() => {
        setUser(undefined);
        handleClose();
        toast.info("Logged out successfully.");
        navigate("/");
      });
  };

  const handleClose = () => {
    props.setOpen(false);
  };

  const user = useStoreState((s) => s.user.item);

  return (
    <Dialog open={props.open} onClose={handleClose}>
      <DialogTitle>Logout</DialogTitle>
      <DialogContent>
        <DialogContentText>
          {user?.fullName}, Are you sure you want to logout?
        </DialogContentText>
      </DialogContent>
      <DialogActions>
          <Button onClick={handleClose} style={{ color:"#aaa" }}>
            Cancel
          </Button>
          <Button onClick={logoutConfirmed} color="primary" autoFocus>
            Logout
          </Button>
        </DialogActions>
    </Dialog>
  );
}
