import axios from "axios";
import IAgency from "../../../estreeh-common-library/build/interfaces/IAgency";
import { IResponse } from "../../../estreeh-common-library/build/interfaces/IResponse";

  export const apiGetAgencies = async () => {
    console.log("Getting agencies");
    const r = await axios.get<IResponse<IAgency[]>>(
      process.env.REACT_APP_API_ROOT + "/pub/agency"
    );
    console.log(`Got ${r.data.data.length} agencies`);
    return r.data.data;
  };