import axios from "axios";
import { IResponse } from "../../../estreeh-common-library/build/interfaces/IResponse";
import { IUser } from "../../../estreeh-common-library/build/interfaces/IUser";

export const apiGetUser = async (): Promise<IUser | undefined> => {
  console.log("Getting user");
  const r = await axios.get<IResponse<IUser | undefined>>(
    process.env.REACT_APP_API_ROOT + "/auth/me",
    { withCredentials: true }
  );
  const user = r.data.data;
  if (user !== undefined)
    if (user.role === "Customer") {
      return user;
    } else {
      await axios.get(process.env.REACT_APP_API_ROOT + "/auth/logout", {
        withCredentials: true,
      });
    }
  return undefined;
};
