import { Action, action, debug, Thunk, thunk } from "easy-peasy";

import { apiGetAgencies } from "../api-calls/ApiGetAgencies";
import { IFilterParameter } from "../../../estreeh-common-library/build/interfaces/IFilterParameter";
import { removeFilterParameter } from "../helpers/RemoveFilterParameter";
import { FilterTwoYearWorkers } from "../helpers/FilterTwoYearWorkers";
import { removeAgencyWithoutTwoYearWorkers } from "../helpers/RemoveAgencyWithoutTwoYearWorkers";
import { AssignTwoYearWorkersToAgency } from "../etl/AssignTwoYearWorkersToAgency";
import { apiGetWorkersTwoYear } from "../api-calls/ApiGetWorkersTwoYear";
import { apiGetUser } from "../api-calls/ApiGetUser";
import { TransformTwoYearWorker } from "../etl/TransformTwoYearWorker";
import { TransformAgency } from "../etl/TransformAgency";
import _ from "lodash";
import {
  AgenciesHourlyOnly,
  AgenciesMonthlyOnly,
} from "../etl/AgenciesLeasedFilters";
import IWorkerRental from "../../../estreeh-common-library/build/interfaces/IWorkerRental";
import { Base_ModelSingular } from "../../../estreeh-common-library/build/interfaces/store/Base_ModelSingular";
import { IBasicFilterableModel } from "../../../estreeh-common-library/build/interfaces/store/IBasicFilterableModel";
import { Base_Model } from "../../../estreeh-common-library/build/interfaces/store/Base_Model";
import { nationalities } from "./Nationalities";
import { jobTitles } from "./FilterOptions";
import { IStoreModel } from "../interfaces/IStoreModel";
import { toast } from "react-toastify";

const makeStoreDefault = (): IStoreModel => {
  return {
    // selectedWorkerTwoYear: twoYear(),
    // selectedSlots: slots(),
    // selectedWorkerRental: hourly(),

    agency: filterableModel(),
    agencyTwoYear: filterableModel(),
    agencyHourly: filterableModel(),
    agencyMonthly: filterableModel(),
    workerTwoYear: filterableModel(),
    workerHourly: filterableModel(),
    workerMonthly: filterableModel(),

    filterTwoYear: arrayModel(),
    filterRental: arrayModel(),
    selectedSlots: arrayModel(),

    user: singleModel(),
    // selectedWorkerTwoYear: singleModel(),
    selectedWorkerRental: singleModel(),
    applicationForm: singleModel(),
    translationPack: singleModel(),
    returnUrl: singleModel(),
    dataLoading: singleModel(false),
    dataLoaded: singleModel(false),
    recent: {
      request: singleModel(),
      requestResponse: singleModel(),
    },
    filterOptions: {
      jobTitle: arrayModel(),
      nationality: arrayModel(),
    },
    transformData: transformData,
    updateFiltersTwoYear: updateFiltersTwoYear,
    clearFilters: thunk((actions) => {
      actions.filterTwoYear.setItems({ payload: [] });
      actions.filterRental.setItems({ payload: [] });
      actions.initiateFilterStates();
    }),
    loadFromApi: thunk(async (actions) => {
      actions.dataLoading.setItem(true);
      console.log("Calling loadFromApi on Store!");
      const workers = await apiGetWorkersTwoYear();
      actions.workerTwoYear.setItems({
        setUnfiltered: true,
        payload: workers,
      });
      const agencies = await apiGetAgencies();
      actions.agency.setItems({
        setUnfiltered: true,
        payload: agencies,
      });

      await actions.loadUser();
      actions.dataLoaded.setItem(true);
      actions.transformData();
      actions.initiateFilterStates();
    }),
    loadUser: thunk(async (actions) => {
      const user = await apiGetUser();
      if (user !== undefined) {
        actions.user.setItem(user);
        toast.success(`Welcome, ${user.fullName}!`)
        // console.log("Found user. Welcome, " + user.fullName + "!");
      }
      else
      {
        // console.log("User response received but no user found (Not logged in?)",user);
      }
      return user;
    }),
    initiateFilterStates: initiateFilterStates,
  };
};

const singleModel = <T>(itemDefault?: T): Base_ModelSingular<T | any> => {
  return {
    item: itemDefault,
    setItem: action((state, payload) => {
      state.item = payload;
    }),
  };
};

const arrayModel = <T>(): Base_Model<T> => {
  return {
    items: [],
    setItems: action((state, payload) => {
      state.items = payload.payload;
    }),
  };
};

const filterableModel = <T>(): IBasicFilterableModel<T> => {
  return {
    items: [],
    unfilteredItems: [],
    setItems: action((state, payload) => {
      state.items = payload.payload;
      if (payload.setUnfiltered === true)
        state.unfilteredItems = payload.payload;
    }),
  };
};

const transformData: Action<IStoreModel> = action((state, payload) => {
  const tPack = state.translationPack.item;
  if (tPack === undefined) {
    console.log("Store: Transform data failed, no translation pack");
    return;
  } else {
    console.log("Transforming data...");
    const workersTwoYear = state.workerTwoYear.items.slice(0);
    const agencies = state.agency.unfilteredItems.slice(0);

    workersTwoYear.forEach((worker) => {
      TransformTwoYearWorker(worker, tPack);
    });

    agencies.forEach((agency, i) => {
      TransformAgency(agency, i, tPack, {
        twoYear: workersTwoYear,
      });
    });

    let workersRentalHour: IWorkerRental[] = [];
    let workersRentalMonth: IWorkerRental[] = [];

    let agenciesContract = _.orderBy(
      agencies.filter((agency) => {
        return agency.hasTwoYearWorkerServices;
      }),
      ["rating"],
      ["desc"]
    );

    let agenciesLeased = _.orderBy(
      agencies.filter((agency) => {
        return agency.hasRentedServices;
      }),
      ["rating"],
      ["desc"]
    );

    let agenciesHourly = AgenciesHourlyOnly(agenciesLeased);
    let agenciesMonthly = AgenciesMonthlyOnly(agenciesLeased);

    agenciesHourly.forEach((agency) => {
      agency.servicesSelectableWorkers?.forEach((service) => {
        workersRentalHour = workersRentalHour.concat(service.workersRental);
      });
    });

    state.workerTwoYear.items = workersTwoYear;
    state.workerTwoYear.unfilteredItems = workersTwoYear;
    state.agencyTwoYear.items = agenciesContract;
    state.agencyTwoYear.unfilteredItems = agenciesContract;
    state.agencyHourly.items = agenciesHourly;
    state.agencyHourly.unfilteredItems = agenciesHourly;
    state.agencyMonthly.items = agenciesMonthly;
    state.agencyMonthly.unfilteredItems = agenciesMonthly;
    state.workerHourly.items = workersRentalHour;
    state.workerHourly.unfilteredItems = workersRentalHour;
    state.workerMonthly.items = workersRentalMonth;
    state.workerMonthly.unfilteredItems = workersRentalMonth;

    console.log("Store: Data transformed");
  }
});

const updateFiltersTwoYear: Thunk<IStoreModel, IFilterParameter | undefined> =
  thunk((actions, filterParameter, helpers) => {
    const state = helpers.getState();
    console.log("Attempting Update Filters Two Year Contracts");

    let prevFilters = state.filterTwoYear.items.slice(0);
    let filters = prevFilters;
    if (filterParameter !== undefined) {
      filters = removeFilterParameter(prevFilters, filterParameter.key);
      if (
        (filterParameter.array !== undefined &&
          filterParameter.array.length > 0) ||
        filterParameter.gt !== undefined ||
        filterParameter.lt !== undefined
      )
        filters.push(filterParameter);
      state.filterTwoYear.items = filters;
      console.log("New filters:");
      console.log(debug(filters));
    }

    const workersTwoYear = FilterTwoYearWorkers(
      state.workerTwoYear.unfilteredItems,
      filters
    );

    const agenciesTwoYear = state.agencyTwoYear.unfilteredItems.slice(0);

    agenciesTwoYear.forEach((agency) => {
      AssignTwoYearWorkersToAgency(agency, workersTwoYear);
    });

    if (filters.length > 0) removeAgencyWithoutTwoYearWorkers(agenciesTwoYear);

    state.agencyTwoYear.items = agenciesTwoYear;
    state.workerTwoYear.items = workersTwoYear;
  });

const initiateFilterStates: Thunk<IStoreModel> = thunk((actions) => {
  actions.filterOptions.nationality.setItems({
    payload: nationalities.map((nationality) => ({
      key: nationality,
      value: false,
    })),
  });
  actions.filterOptions.jobTitle.setItems({
    payload: jobTitles.map((jobTitle) => ({
      key: jobTitle,
      value: false,
    })),
  });

  actions.updateFiltersTwoYear();
});

export const storeDefault: IStoreModel = makeStoreDefault();
