import { Box, Button, Grid, Typography } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import NiceBox from "../common/NiceBox";
import RequestWorkerContainer from "./RequestWorkerContainer";
import LabelValueLine from "../common/LabelValueLine";
import { navigate } from "@reach/router";
import RequestPaymentSummary from "./RequestPaymentSummary";
import { IRequest } from "../../../../estreeh-common-library/build/interfaces/IRequest";
import RequestScheduleDisplay from "./RequestScheduleDisplay";
import ErrorMessage from "../common/ErrorMessage";
import { useState } from "react";
import axios, { AxiosResponse } from "axios";
import { IApiResNewRequest } from "../../../../estreeh-common-library/build/interfaces/api/res/IApiResNewRequest";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";
import { toast } from "react-toastify";

interface Params {}

interface Props extends RouteComponentProps<Params> {
  link: string;
}

const RequestConfirmationPage = (props: Props) => {
  const [inProgress, setInProgress] = useState(false);
  let errorMessage = "";
  const buttonSize = "medium";
  const oneLine = true;
  const user = useStoreState((s) => s.user.item);
  let userId = user && user._id ? user._id : "NewUser";

  const applicationForm = useStoreState((s) => s.applicationForm.item);
  const selectedWorkerRental = useStoreState(
    (s) => s.selectedWorkerRental.item
  );

  const setAppForm = useStoreActions((a) => a.applicationForm.setItem);
  const setRecentRequest = useStoreActions((a) => a.recent.request.setItem);
  const setRecentRequestResponse = useStoreActions(
    (a) => a.recent.requestResponse.setItem
  );

  if (
    applicationForm === undefined ||
    applicationForm.workerTwoYear === undefined
  ) {
    errorMessage += "Application Form data is undefined";
    return <ErrorMessage message={errorMessage} />;
  } else {
    const onClick = () => {
      setInProgress(true);

      let request: IRequest = {
        applicationForm: applicationForm,
        trackingCode: "",
        // user: user,
        workerTwoYear: applicationForm.workerTwoYear,
        agencyId: applicationForm.workerTwoYear?.agencyId
          ? applicationForm.workerTwoYear?.agencyId
          : "NoID",
        isLeaseByHour: true,
        isLeaseByMonth: false,
        isTwoYearContract: false,
        statusCode: 1000,
        statusHistory: [],
        statusRemarks: "",

        userId: userId,
        created: new Date(),
        modified: new Date(),
      };

      axios
        .post<IRequest, AxiosResponse<IResponse<IApiResNewRequest>>>(
          process.env.REACT_APP_API_ROOT + "/customer/requests",
          request,
          {
            withCredentials: true,
          }
        )
        .then((response) => {
          console.log("Successfully created new request:");

          setAppForm(undefined);
          setRecentRequest(request);
          setRecentRequestResponse(response.data.data);

          navigate(props.link);
        })
        .catch((reason) => {
          toast.error(reason.response.message);
        })
        .finally(() => {
          setInProgress(false);
        });
    };

    return (
      <NiceBox p={4}>
        <Grid container spacing={1} direction="row-reverse">
          <Grid item xs={12} md={6}>
            <Box py={2}>
              <Typography variant="h6">Worker Details</Typography>
            </Box>
            <RequestWorkerContainer
              workerRental={applicationForm?.workerRental}
              workerTwoYear={applicationForm?.workerTwoYear}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <RequestScheduleDisplay applicationForm={applicationForm} />
            <Box py={2}>
              <Typography variant="h6">Application Details</Typography>
            </Box>
            <Box>
              <Grid container>
                <LabelValueLine title="Full Name" oneLine={oneLine}>
                  {applicationForm?.fullName}
                </LabelValueLine>
                <LabelValueLine title="Mobile" oneLine={oneLine}>
                  {applicationForm?.mobileNumber}
                </LabelValueLine>
                <LabelValueLine title="Other Mobile" oneLine={oneLine}>
                  {applicationForm?.otherMobileNumber}
                </LabelValueLine>
                {applicationForm.email && (
                  <LabelValueLine title="Email" oneLine={oneLine}>
                    {applicationForm?.email}
                  </LabelValueLine>
                )}
                <LabelValueLine title="Special Notes" oneLine={oneLine}>
                  {applicationForm?.specialNotes}
                </LabelValueLine>
                {applicationForm.address && (
                  <LabelValueLine title="Address" oneLine={oneLine}>
                    {applicationForm?.address}
                  </LabelValueLine>
                )}
                {/* <LabelValueLine title="Frequency" oneLine={oneLine}>{applicationForm?.frequency}</LabelValueLine> */}
              </Grid>
            </Box>
            <RequestPaymentSummary
              paymentItems={applicationForm?.paymentSummaryItems}
              workerRental={selectedWorkerRental}
              remarks={applicationForm.paymentRemarks}
            />
          </Grid>
        </Grid>
        <Box py={2} my={3} textAlign="center">
          <Button
            variant="contained"
            color="primary"
            size={buttonSize}
            onClick={onClick}
            disabled={inProgress}
          >
            {inProgress ? "Wait..." : "Confirm"}
          </Button>
        </Box>
      </NiceBox>
    );
  }
};

export default RequestConfirmationPage;
