import { Box, Icon } from "@material-ui/core";
import { Link } from "@reach/router";
import React from "react";
import UseStyles from "../../config/UseStyles";

const WizardCard = (props: {
  to: string;
  children: any;
  icon?: any;
  title?:string;
  isMd: boolean;
  disabled: boolean;
}) => {
  const classes = UseStyles();

  return (
    <Link
      to={props.disabled ? "" : props.to}
      className={
        props.disabled ? classes.wizardCardLinkDisabled : classes.wizardCardLink
      }
      onClick={!props.disabled? undefined : () => {
        // TODO: Make this message nicer?
        alert(`"${props.title}" services are Coming soon!`);
      }}
    >
      <Box py={4} px={2} color={props.disabled ? "#bbb" : undefined}>
        <Box>
          {/* <span className="MuiIcon-root material-icons-two-tone MuiIcon-colorPrimary" style={{ fontSize: 50 }}>{props.icon}</span> */}
          <Icon
            // className="material-icons-two-tone"
            // color="primary"
            style={{ fontSize: 50 }}
          >
            {props.icon}
          </Icon>
        </Box>
        <Box fontSize={props.isMd ? "h6.fontSize" : 15}>{props.children}</Box>
      </Box>
    </Link>
  );
};

export default WizardCard;
