import { Box, Button, Grid, Link, Typography } from "@material-ui/core";
import axios from "axios";
import React, { ReactElement, useEffect, useState } from "react";
import { TextField } from "../../../../estreeh-common-library/node_modules/@material-ui/core";
// import { IApiReqSendOtp } from "../../../../estreeh-common-library/build/interfaces/api/req/IApiReqSendOtp";
import { IApiResCheckMobile } from "../../../../estreeh-common-library/build/interfaces/api/res/IApiResCheckMobile";
// import { IApiResSendOtp } from "../../../../estreeh-common-library/build/interfaces/api/res/IApiResSendOtp";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import LoginHeader from "./LoginHeader";
import MobileInput from "./MobileInput";

type D<T> = React.Dispatch<React.SetStateAction<T>>;

interface Props {
  mobile: string;
  errored: boolean;
  loading: boolean;
  newUser?: boolean;
  otpSent: boolean;
  checkMobileResult: IApiResCheckMobile | undefined;
  allowResend: Date;
  secret: string;
  setMobile: D<string>;
  setNewUser: D<boolean | undefined>;
  setLoading: D<boolean>;
  setOtpSent: D<boolean>;
  setCheckMobileResult: D<IApiResCheckMobile | undefined>;
  setSecret: D<string>;
  sendOtp?: React.MouseEventHandler<HTMLButtonElement>;
  // validateOtp?: React.MouseEventHandler<HTMLButtonElement>;
  validateOtp?: () => void;
  setIntent: React.Dispatch<React.SetStateAction<"login" | "signUp">>;
}

export default function LoginEnterMobile({
  mobile,
  errored,
  loading,
  newUser,
  otpSent,
  allowResend,
  secret,
  sendOtp,
  validateOtp,
  setMobile,
  setNewUser,
  setLoading,
  setOtpSent,
  setCheckMobileResult,
  setSecret,
  setIntent,
}: Props): ReactElement {
  const line2My = 3;
  const boxWidth = 200;
  const [countdown, setCountdown] = useState(0);

  useEffect(() => {
    const interval = setInterval(
      () => setCountdown(secondsBetweenDates(new Date(), allowResend)),
      500
    );
    if (mobile.length === 8 && newUser === undefined && loading === false) {
      // TODO: Validate mobile before doing axios
      setLoading(true);
      axios
        .post<IResponse<IApiResCheckMobile | undefined>>(
          process.env.REACT_APP_API_ROOT + "/otp/check-mobile",
          { mobile: `+973${mobile}` }
        )
        .then((result) => {
          const checkMobile = result.data.data;
          setCheckMobileResult(checkMobile);
          if (checkMobile !== undefined) {
            setNewUser(checkMobile.userCount === 0);
            setOtpSent(checkMobile.otpCount !== 0);
          }
        })
        .finally(() => {
          setLoading(false);
        });
    }
    if (mobile.length !== 8 && newUser !== undefined) {
      setLoading(false);
      setNewUser(undefined);
    }
    if (
      secret.length === 5 &&
      otpSent === true &&
      validateOtp !== undefined &&
      loading === false &&
      newUser !== undefined
    ) {
      validateOtp();
    }
    return function cleanup() {
      clearInterval(interval);
    };
  }, [
    allowResend,
    loading,
    mobile,
    newUser,
    otpSent,
    secret.length,
    setCheckMobileResult,
    setLoading,
    setNewUser,
    setOtpSent,
    validateOtp,
  ]);

  // FIXME: Bug in production, interface is unclickable ?

  return (
    <>
      <LoginHeader title="Enter Mobile" />
      <Box maxWidth={boxWidth} my={2} mx="auto">
        <MobileInput
          value={mobile}
          isErrored={errored}
          disabled={loading}
          onChange={(event) => {
            if (event.target.value.match(/^\d*$/)) {
              setMobile(event.target.value);
            }
          }}
        />
      </Box>
      {!otpSent && (
        <Box my={line2My}>
          <Typography>Verification SMS will be sent to your mobile.</Typography>
        </Box>
      )}
      {otpSent && (
        <Box maxWidth={boxWidth} mx="auto">
          <TextField
            variant="outlined"
            label="Enter OTP"
            type="tel"
            value={secret}
            onChange={(event) => setSecret(event.target.value)}
            inputProps={{
              maxLength: 5,
            }}
          />
        </Box>
      )}
      <Box m={2}>
        <Grid container spacing={2} justify="center">
          <Grid item>
            <Button
              variant={otpSent ? "outlined" : "contained"}
              color="primary"
              disabled={
                loading ||
                newUser === undefined ||
                newUser !== true ||
                countdown > 0
              }
              onClick={sendOtp}
            >
              {!loading
                ? otpSent
                  ? countdown > 0
                    ? `Resend (${countdown})`
                    : "Resend OTP"
                  : "Send OTP"
                : "Checking..."}
            </Button>
          </Grid>
          {otpSent && (
            <Grid item>
              <Button
                variant="contained"
                color="primary"
                disabled={loading}
                onClick={validateOtp}
              >
                Validate
              </Button>
            </Grid>
          )}
        </Grid>
      </Box>
      {!otpSent && (
        <Box m={2}>
          Already have an account?
          <br />
          <Link
            style={{ cursor: "pointer" }}
            onClick={() => setIntent("login")}
          >
            Click here to Login
          </Link>
        </Box>
      )}
    </>
  );
}

function secondsBetweenDates(date1: Date, date2: Date) {
  const secs = Math.round((date2.getTime() - date1.getTime()) / 1000);
  return secs > 0 ? secs : 0;
}
