import axios from "axios";
import { IResponse } from "../../../estreeh-common-library/build/interfaces/IResponse";
import IWorkerTwoYear from "../../../estreeh-common-library/build/interfaces/IWorkerTwoYear";

export const apiGetWorkersTwoYear = async() => {
  console.log("Getting workers");
  const r = await axios.get<IResponse<IWorkerTwoYear[]>>(
    process.env.REACT_APP_API_ROOT + "/pub/workertwoyear"
  );
  console.log(`Got ${r.data.data.length} workers`);
  return r.data.data;
}